.login-modal{
    padding: 10px 20px;
    width: 350px;
    height: 350px;
    gap: 15px;
    text-align: center;
    margin: 50px auto;
    h1{
        border-bottom: 1px solid #222;
        margin-bottom: 10px;
        width: 100%;
    }
    button{
        padding: 10px;
        border-radius: 30px;
        background: #a2c7e5;

    }
    button:hover{
        cursor: pointer;
    }
    input{

        padding: 10px;
        border-radius: 30px;

    }
}