.app-header{
    text-align: center;
    padding-top: 20px;
    height: 9vh;
    background: #808a9f;
    color: #fff;

    padding: 20px;
 a:first-child(){
    color: #fff;
 }
 a.my-active:first-child(){
    color: #fff;
 }
a{
    color: #fff;
    font-weight: 700;
}
a.active, a.my-active {
    color: #0f0080;
}
.logo{
    border-radius: 50%;
}
}
.navigation{
    gap: 5px;
    justify-content: center;
    button{
        background: none;
        height: 30px;
        border: none;
        text-decoration: underline;
        cursor: pointer;
    }
}