.contact-details{
    padding: 20px;
    gap: 15px;
    img{
        width: 150px;
        height: 150px;
        filter: drop-shadow( 8px 8px 8px #19adfc);
        margin-bottom: 20px;
    }
    a.exit{
        cursor: pointer;
        margin-top: 80px;
        text-decoration: none;
        display: block;
        color: #fff;
        font-weight: 600;
        font-size: 20px;
    }
    .details-box{
        gap: 10px;
    }

}

.transfer{
    form{
        margin-top: 15px;
        font-size: 16px;
        font-weight: 550;
        input{
            margin: 0 6px;
        }
        button{
            padding: 3px;
            border-radius: 30px;
            cursor: pointer;
        }
    }
    button{
        margin-top: 7px;
        width: 60px;
    }
    .list-info{
        font-size: 16px;
        font-weight: 550;
    }
}

.period-btns{
    button{
        background: rgb(100, 113, 139);
        border-radius: 7px;
        width: 50px;
        height: 25px;
    }
}
