body {
  background: $gray-50;

}

.container {
  min-height: 80vh;
}
.main-app{
 background-attachment: fixed;
 background-image: linear-gradient(180deg,hsla(0,100%,99%,.1),hsla(49,82%,93%,.1)),url('../../imgs/background.jpg');
 background-position: 50%;
 background-repeat: no-repeat;
 background-size: cover;
 min-height: 100vh;
 color: white;
}
.statistic-page{
  background-color: lightgray;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-height: 50vh;
}