.contact-preview{
 padding: rem(15px);
border: 1px solid #222;
width: 250px;
height: 200px;
cursor: pointer;
background: #ccc;
border-radius: 30px;
padding: 5px;
box-shadow: 0 0 40px #000;
img {
    width: 50%;
    border: 5px solid transparent;
    border-radius: 50%;
    justify-self: center;
    margin-block-end: rem(10px);
    transition: all 0.3s;
}
h2 {
    margin-block-start: rem(10px);
}
a{
    position: unset !important;
    top: unset !important;
    right: unset !important;
}
}