.contact-edit {
   gap: 15px;
    img{
        width: 100px;
        height: 100px;
        filter: drop-shadow( 8px 8px 8px #19adfc);
    }
    form {
        gap: 7px;
        input {
            width: 200px;
            height: 30px;
            border-radius: 12px;
            margin: 5px;

        }

        input::placeholder {
            padding: 5px;
        }

        button {
            padding: 3px;
            width: 100px;
        }
    }
    a{
        margin-top: 50px;
        cursor: pointer;
        text-decoration: none;
        display: block;
        color: #fff;
        font-weight: 600;
        font-size: 20px;
    }
    button {
        padding: 3px;
        width: 100px;
    }
}